
.q-music .toplist-songlist li a{
  color: #fff;
}
.toplist-wrapper .singer-name{
  text-align: left;
  text-indent: 12px;
  color: #cccccc;
}
.q-music .toplist-songlist  .singer-name a{
  color: #cccccc;
}
.toplist-wrapper .list-name-s{
  text-align: center;
  z-index: 10;
  position: relative;
  margin-top: 50px;
  font-size: 21px;
}
#app .q-music .toplist-wrapper li:hover .mask{
  opacity: .4;
}
#app .q-music .toplist-wrapper li:hover .bg{
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.toplist-wrapper .mask{
  width: 100%;
  height: 100%;
  position: absolute;
  top:0;
  left: 0;
  z-index: 1;
  background: #000;
  opacity: 0;
  transition: all .3s;
}
.toplist-wrapper .list-name-b{
  text-align: center;
  z-index: 10;
  position: relative;
  font-weight: 400;
  font-size: 36px;
}
#app .q-music .toplist-wrapper li{
  overflow: hidden;
  position: relative;
  height: 567px;
  width: 25%;
  color: #fff;
}
.top-wrapper{
  width: 1200px;
  height: 567px;
  margin: 0 auto;
}
.toplist-wrapper .bg{
  transition: all .3s;
  width: 100%;
  height: 100%;
  position: absolute;
  top:0;
  left: 0;
  z-index: 0;
  background: url("https://y.gtimg.cn/mediastyle/yqq/img/bg_index_top.jpg?max_age=2592000&v=2591d4829162dfda6b9112c771edda86") no-repeat;
}
.toplist-wrapper .bg-one{
  background-position: 0px 0px;
}
.toplist-wrapper .bg-two{
  background-position: -300px 0px;
}
.toplist-wrapper .bg-three{
  background-position:-600px 0px ;
}
.toplist-wrapper .bg-four{
  background-position:-900px 0px ;
}
.top-page{
  padding: 50px 0;
  width: 100%;
  background: #121212 url(https://y.gtimg.cn/mediastyle/yqq/img/bg_index_top2.jpg) 50% 0 no-repeat;
  min-height: 500px;
}
.top-page .page-title{
  margin-bottom: 30px;
}
.hot-page .page-title{
  margin-top: 20px;
}
.hot-page .page-title span{
  color: #333;
}
.listen-num{
  font-size: 14px;
  color: #666666;
  text-align: center;
  margin-top: 5px;
}
.singer-name{
  font-size: 14px;
  color: #666666;
  cursor: pointer;
  text-align: center;
}
.singer-name:hover{
  color: #42b983;
}
.mv-title{
  text-align: center;
  height: 30px;
  line-height: 30px;
  font-size: 16px;
  color: #333;
  margin: 10px auto 3px auto;
}
.mv-page{
  background: #e6e6e6;
  padding-top: 50px;
}
.shoufa-img{
  position: relative;
  overflow: hidden;
}
.shoufa-img img{
  transition: all .5s;
  min-height: 171px;
}
.shoufa-img:hover .icon-play{
  -webkit-transform: scale(1);
          transform: scale(1);
}
.shoufa-img:hover img{
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.mv-page .page-title span{
  color: #333;
}
#app .q-music .toplist-songlist{
  width: 180px;
  margin: 80px auto 0 auto;
}
#app .q-music .toplist-songlist li{
  width: 100%;
  height: 60px;
  position: relative;
  z-index: 10;
  float: inherit;
}
#app .q-music .shoufa-mv-list li{
  float: left;
  width: 25%;
  height: 260px;
  margin-top: 15px;
}
.shoufa-wrapper,.recommend-wrapper,.hot-wrapper{
  color: #fff;
  width: 1180px;
  margin: 50px auto;
  position: relative;
}
.recommend-wrapper{
  width: 100%;
}
.shoufa-nav{
  text-align: center;
}
.shoufa-nav span{
  display: inline-block;
  cursor: pointer;
  margin: 10px 35px;
  color: #999;
}
.shoufa-nav span:hover{
  color: #333;
}
.shoufa-nav .active{
  color: #333;
}
.shoufa-page{
  height: 714px;
  background: url("https://y.gtimg.cn/mediastyle/yqq/img/bg_index_new.jpg") repeat-x;
  padding-top: 20px;
}
.page-title{
  text-align: center;
}
.page-title span{
  color: #fff;
  font-weight: normal;
  font-size: 36px;
  height: 40px;
  line-height: 40px;
  position: relative;
}
.page-title span:after{
  content: '';
  position: absolute;
  width: 100px;
  height: 1px;
  background: #666;
  top: 19px;
  left: 180px;
  display: inline-block;
}
.page-title span:before{
  content: '';
  position: absolute;
  width: 100px;
  height: 1px;
  background: #666;
  top: 19px;
  right: 180px;
  display: inline-block;
}
.home {
  width: 100%;
  text-align: left;
  background: #fff;
  padding: 40px 0;
  margin-top: 30px;
}
.home .home-wrapper{
  width: 100%;
  position: relative;
  margin:0 auto;
}



