
.star span {
  display: inline-block;
}
.score{
  font-style: normal;
  color: #FF6633;
  position: relative;
  top: -3px;
  font-size: 16px;
}
.star .on{
  background: url("../assets/img/star-on.png") no-repeat;
  background-size: contain;
}
.star .half{
  background: url("../assets/img/star-half.png") no-repeat;
  background-size: contain;
}
.star .off{
  background: url("../assets/img/star-off.png") no-repeat;
  background-size: contain;
}
.typeIn span{
  width: 18px;
  height:18px;
  display: inline-block;
  margin: 2px;
}
.typeSmall span{
  width: 16px;
  height:16px;
  display: inline-block;
  margin: 1px;
}



