
#app .q-music .mv-list li{
  width: 20%;
}
.mv-title{
  overflow: hidden;
  text-overflow: ellipsis;
}
.mv-nick{
  font-size: 14px;
  color: #999999;
  text-align: center;
}
.singer-detail h2{
  margin: 20px 0 5px 0;
  font-weight: normal;
}
#app .q-music .mv-list .mv-img-box{
  position: relative;
  width: 210px;
  height: 113px;
  margin-right: 20px;
}
.album-name{
  margin: 12px 0;
  color: #333;
  font-style: 16px;
  overflow: hidden;
  text-overflow: ellipsis;
  height: 18px;
  line-height: 18px;
}
.pub-time{
  color: #999;
  font-size: 14px;
  overflow: hidden;
  text-overflow: ellipsis;
  height: 14px;
  line-height: 14px;
}
.album-list{
  padding-bottom: 20px;
}
#app .q-music .album-list li{
  float: left;
  width: 20%;
}
.album-list li .album-box img{
  transition: all .5s;
}



