
* {
  padding: 0;
  margin: 0;
}
.love-heart {
  position: absolute;
  right: 0px;
  bottom: 10px;
  height: 50px;
  line-height: 72px;
}
.focus-out, .focus-scale, .focus-in {
  display: inline-block;
  width: 26px;
  height: 26px;
  background: url("../assets/img/follow.png") no-repeat;
  background-size: cover;
  position: absolute;
  top: 5px;
  left: 28px;
}
.focus-scale {
  background-position: left bottom;
  -webkit-animation: focusedScale .5s 1 ease 0s;
          animation: focusedScale .5s 1 ease 0s;
}
.focus-out {
  background-position: left top;
}
.focus-in {
  background-position: left bottom;
}
.focus-info {
  color: #fff;
  font-size: 14px;
  display: block;
  width: 80px;
  text-align: center;
}
.star {
  float: none;
}
@-webkit-keyframes focusedScale {
0% {
    transform: scale(0, 0);
    -webkit-transform: scale(0, 0);
    transform-origin: center center;
    -webkit-transform-origin: center center;
    opacity: .5;
}
20% {
    transform: scale(.6, .6);
    -webkit-transform: scale(.6, .6);
    transform-origin: center center;
    -webkit-transform-origin: center center;
    opacity: .5;
}
40% {
    transform: scale(1.2, 1.2);
    -webkit-transform: scale(1.2, 1.2);
    transform-origin: center center;
    -webkit-transform-origin: center center;
    opacity: .5;
}
60% {
    transform: scale(1.5, 1.5);
    -webkit-transform: scale(1.5, 1.5);
    transform-origin: center center;
    -webkit-transform-origin: center center;
    opacity: .4;
}
80% {
    transform: scale(1.8, 1.8);
    -webkit-transform: scale(1.8, 1.8);
    transform-origin: center center;
    -webkit-transform-origin: center center;
    opacity: .3;
}
100% {
    transform: scale(2, 2);
    -webkit-transform: scale(2, 2);
    transform-origin: center center;
    -webkit-transform-origin: center center;
    opacity: .2;
}
}
@keyframes focusedScale {
0% {
    transform: scale(0, 0);
    -webkit-transform: scale(0, 0);
    transform-origin: center center;
    -webkit-transform-origin: center center;
    opacity: .5;
}
20% {
    transform: scale(.6, .6);
    -webkit-transform: scale(.6, .6);
    transform-origin: center center;
    -webkit-transform-origin: center center;
    opacity: .5;
}
40% {
    transform: scale(1.2, 1.2);
    -webkit-transform: scale(1.2, 1.2);
    transform-origin: center center;
    -webkit-transform-origin: center center;
    opacity: .5;
}
60% {
    transform: scale(1.5, 1.5);
    -webkit-transform: scale(1.5, 1.5);
    transform-origin: center center;
    -webkit-transform-origin: center center;
    opacity: .4;
}
80% {
    transform: scale(1.8, 1.8);
    -webkit-transform: scale(1.8, 1.8);
    transform-origin: center center;
    -webkit-transform-origin: center center;
    opacity: .3;
}
100% {
    transform: scale(2, 2);
    -webkit-transform: scale(2, 2);
    transform-origin: center center;
    -webkit-transform-origin: center center;
    opacity: .2;
}
}
.avatar {
  display: inline-block;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  overflow: hidden;
}
.performer {
  text-align: left;
  line-height: 44px;
  color: #fff;
  height: 44px;
  position: relative;
  padding-left: 20px;
}
.performer i {
  font-style: normal;
  position: absolute;
  top: -1px;
  left: 70px;
}
.cast {
  position: absolute;
  width: 100%;
  height: 180px;
  background: rgba(0, 0, 0, 0.8);
  left: 0;
  bottom: -180px;
  transition: all .5s;
}
.box {
  color: #fff;
  position: absolute;
  bottom: 8px;
  width: 152px;
  right: 0;
}
.category {
  margin-bottom: 10px;
}
.category .current {
  background: #fff;
  color: #2ad487;
}
.category span {
  display: inline-block;
  padding: 2px 4px;
  height: 22px;
  line-height: 26px;
  cursor: pointer;
  background: #2ad487;
  color: #fff;
  margin: 2px;
  border-radius: 2px;
}
.current {
  background: #2ad487;
  color: #fff;
}
.recent-box {
  position: relative;
}
.recent {
  position: absolute;
  width: 380px;
  background: #fff;
  opacity: 0.9;
  margin: 0 auto;
  left: 50%;
  margin-left: -190px;
  z-index: 1000;
  color: #2ad487;
  text-align: left;
  top: 35px;
}
.recent li {
  padding: 5px;
}
.recent li span {
  float: right;
  color: #ccc;
}
.recent .last-li {
  border-top: 1px solid #ccc;
}
.recent li a {
  color: #000;
  float: right;
}
ul, li {
  list-style: none
}
.new {
  position: absolute;
  width: 30px;
  height: 18px;
  text-align: center;
  line-height: 18px;
  background: red;
  color: #fff;
  border-radius: 5px;
  right: 10px;
  top: 10px;
}
.search {
  width: 100px;
  height: 33px;
  text-align: center;
  line-height: 33px;
  display: inline-block;
  background: #2ad487;
  color: #fff;
  cursor: pointer;
  position: relative;
  top: 1px;
  left: -5px;
}
input {
  border: none;
  outline: none;
  height: 31px;
  border: 1px solid #2ad487;
  width: 270px;
}
body {
  background: #151515;
  font-family: "微软雅黑"
}
img {
  max-width: 100%
}
a {
  text-decoration: none;
  cursor: pointer;
}
.left {
  float: left;
}
.right {
  float: right;
}
.icon {
  width: 16px;
  height: 16px;
  vertical-align: middle;
}
.clearfix {
}
.clearfix::after {
  content: "";
  width: 100%;
  height: 0;
  clear: both;
  display: block;
}
.content {
  width: 1200px;
  margin: 0 auto;
}
h1 {
  color: #2ad487;
  line-height: 80px;
  border-bottom: 3px solid #2ad487;
  background-color: #222;
  text-indent: 20px;
  margin-bottom: 20px;
  text-align: left;
}
.movie-list-us li {
  overflow: auto;
  height: 500px;
  position: relative;
}
ul.movie_list li {
  background: #222;
  float: left;
  width: 280px;
  margin: 10px; /*#2ad487*/;
  padding: 12px;
  height: 500px !important;
  transition: all 500ms;
  box-sizing: border-box;
  position: relative;
}
ul.movie_list li:hover {
  border-color: #2ad487;
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
  -webkit-transform: translate3d(0, -3px, 0);
  transform: translate3d(0, -3px, 0);
}
ul.movie_list li:hover .cast {
  bottom: 0px;
}
ul.movie_list li .movie_list_img {
  height: 350px;
  background-size: 100% 100%;
  margin-bottom: 10px
}
ul.movie_list li .title {
  color: #ddd;
  font-weight: 700;
  font-size: 16px;
  margin-bottom: 5px
}
ul.movie_list li .original_title {
  color: #999;
  font-size: 12px
}
.page {
  width: 300px;
  height: 30px;
  margin: 20px auto;
  color: #fff;
  position: relative;
  overflow: hidden;
}
ul.pageCon {
  position: absolute;
  top: 0;
  left: 0;
  width: 200%
}
.rank {
  display: inline-block;
  color: #fff;
  border-radius: 50%;
  background: #2ad487;
  width: 28px;
  height: 28px;
  text-align: center;
  line-height: 28px;
  margin-bottom: 4px;
}
.first {
  background: #FF6633;
}
.second {
  background: #3377cc;
}
.third {
  background: #42b983;
}
ul.pageCon li {
  float: left;
  height: 30px;
  line-height: 30px;
  background: #333;
  box-sizing: border-box;
  border: 1px solid #666;
  width: 30px;
  text-align: center;
  cursor: pointer;
}
ul.pageCon li.active {
  background: #2ad487
}
.fade-enter-active, .fade-leave-active {
  transition: opacity .5s
}
.fade-enter, .fade-leave-active {
  opacity: 0
}



