
.login-in{
  position: absolute;
  left: -66px;
  top:84px;
  cursor: pointer;
  text-decoration: underline;
}
.user-info{
  position: absolute;
  text-align: center;
  left: -200px;
  top:0px;
  width: 200px;
}
.user-info img{
  width: 60px;
  display: block;
  border-radius: 50%;
  margin-left: 65px;
}
.clear-fix:after{content:".";display:block;height:0;clear:both;visibility:hidden
}
.clear-fix{*+height:1%;
}
.panel-music li{
  cursor: pointer;
}
body{
  padding-bottom: 60px;
}
.panel li .delete{
  width: 9px;
  height: 11px;
  background: url("./assets/img/delete.png");
  display: inline-block;
  position: absolute;
  right: 10px;
  top: 10px;
  background-size: cover;
  display: none;
}
.panel p{
  text-align: center;
}
.panel ul{
  margin-left: 10px;
  margin-top: 10px;
}
.panel li{
  transition: all .5s;
  width: 100px;
  height: 130px;
  background: #fff;
  float: left;
  text-align: center;
  margin-bottom: 10px;
  margin-right: 10px;
  padding: 5px;
  position: relative;
}
.panel li:hover .delete{
  display: inline-block;
}
.panel li p{
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #42b983;
}
.panel-body{
  width: 810px;
  height: 100%;
  transition: all .5s;
}
.panel-body .panel{
  width:270px;
  float: left;
  text-align: left;
  height: 100%;
  overflow-y: auto;
}
.panel-nav{
  width:270px;
}
.panel-nav li{
  height: 37px;
  border-bottom: 3px solid #FFFFFF;
  background: #fff;
  width: 90px;
  float: left;
  text-align: center;
  line-height: 37px;
  cursor: pointer;
}
.panel-nav li.current{
  border-color: #ff6633;
  color: #ff6633;
}
.panel-head{
  height: 40px;
  line-height: 40px;
  text-align: left;
  text-indent: 16px;
  color: rgb(94, 80, 80);
}
.panel-head a{
  color: rgb(94, 80, 80);
}
.panel-head .close-follow{
  position: absolute;
  right: 10px;
  top:0px;
  font-size: 30px;
  transition: all .5s;
  cursor: pointer;
  display: inline-block;
  text-align: center;
  text-indent: 0;
}
.panel-head .close-follow:hover{
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.toolbar {
  position: fixed;
  right: 0px;
  top: 0;
  width: 0;
  height: 100%;
  border-right: 6px solid #42b983;
  transition: right .3s ease-in-out 0s;
  z-index: 100;
}
.tool-left{
  right: 270px;
}
.tool-start{
  right: 0px;
}
.toolbar-panels {
  position: absolute;
  left: 6px;
  _left: 6px;
  top: 0;
  width: 270px;
  height: 100%;
  z-index: 200;
  background: #eceaea;
  overflow: hidden;
}
.toolbar-tabs {
  position: absolute;
  top: 50%;
  left: -29px;
  width: 29px;
}
.toolbar-tabs .tabs {
  position: relative;
  width: 38px;
  height: 29px;
  background: #42b983;
  border-radius: 2px;
  left: 0px;
  z-index: 100;
  transition: all .5s;
  margin-top: 2px;
  cursor: pointer;
}
.toolbar-tabs .tabs img {
  position: absolute;
  transition: all .5s;
  min-width: 90px !important;
  left: -134px;
  -webkit-transform: scale(0);
          transform: scale(0);
  top: -90px;
  -webkit-transform-origin: right bottom;
          transform-origin: right bottom;
  opacity: 0;
}
.toolbar-tabs .tabs:hover img {
  left: -92px;
  -webkit-transform: scale(1);
          transform: scale(1);
  opacity: 1;
}
.toolbar-tabs .tabs:hover em {
  left: -92px;
  background: #ff6633;
}
.toolbar-tabs .tabs:hover, .toolbar-tabs .tabs:hover i {
  background-color: #ff6633;
}
.toolbar-tabs .tabs i {
  transition: all .5s;
  width: 29px;
  height: 29px;
  background: url("./assets/img/follow-icon.png") no-repeat;
  background-position: 2px;
  background-color: #42b983;
  background-size: 17px 15px;
  border-radius: 2px;
  position: absolute;
  display: inline-block;
  z-index: 111;
  left: 6px;
  top: 0;
}
.toolbar-tabs .tab-game i {
  background: url("./assets/img/game-icon.png") no-repeat;
  background-position: 2px;
}
.toolbar-tabs .tab-calcu i {
  background: url("./assets/img/calculator-icon.png") no-repeat;
  background-position: 2px;
}
.toolbar-tabs .tab-top i {
  background: url("./assets/img/top-icon.png") no-repeat;
  background-position: 1px;
}
.toolbar-tabs .tab-code i {
  background: url("./assets/img/code-icon.png") no-repeat;
  background-position: 1px;
}
.toolbar-tabs .tabs em {
  transition: all .5s;
  width: 96px;
  height: 29px;
  background: #42b983;
  position: absolute;
  display: inline-block;
  font-style: normal;
  left: 34px;
  z-index: 100;
  top: 0;
  color: #fff;
  border-radius: 4px;
  line-height: 29px;
}
.fadeScale-enter-active, .fadeScale-leave-active {
  transition: all .5s;
}
.fadeScale-enter .game-wrapper, .fadeScale-leave-active .game-wrapper, .fadeScale-enter .calculator, .fadeScale-leave-active .calculator {
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
  opacity: 0
}
.my-game {
  position: absolute;
  left: -80px;
  bottom: 0px;
  cursor: pointer;
  background: #ff6633;
  border-radius: 4px;
  padding: 2px 4px;
}
.animation_paused {
  -webkit-animation-play-state: paused;
  animation-play-state: paused;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 70px;
}
body {
  overflow-x: hidden;
}
.weather-box {
  position: absolute;
  width: 405px;
  color: #FFFFFF;
  right: 0px;
  top: 12px;
  text-align: left;
}
.weather-wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.down-count {
  text-indent: 15px;
  background: #2ad487;
  width: 92%;
  border-radius: 4px;
  margin-top: 2px;
}
.weather-wrapper .weather-left {
  text-align: center;
  vertical-align: middle;
  border-right: 1px solid #2ad487;
  padding-right: 10px;
  margin-right: 10px;
  font-size: 30px;
  line-height: 84px;
}
.nav {
  clear: both;
  height: 55px;
}
.nav li {
  float: left;
  margin: 20px;
}
.nav li a {
  color: #2ad487;
}
.router-link-active {
  background: #fff;
  padding: 2px 6px;
  border-radius: 2px;
}
.custom-classes-transition-enter-active, .custom-classes-transition-leave-active {
  transition: all .5s
}
.custom-classes-transition-enter {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}
.custom-classes-transition-leave-active {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}



