
#app .q-music  .digital-list .digital-list-item{
  width: 20%;
  float: left;
}
.digital-img-box{
  width: 100%;
  overflow: hidden;
}
.digital-img-box :hover{
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.digital-img-box img{
  cursor: pointer;
  transition: all .5s;
}
#app .q-music li .item-title,#app .q-music li .item-author a{
  height: 20px;
  line-height: 20px;
  font-size: 14px;
  width: 100%;
  color: #333;
  overflow: hidden;
  text-overflow: ellipsis;
}
#app .q-music li .item-author a:hover{
  color: #42b983;
}
#app .q-music li .item-price{
  color: #666;
  font-size: 16px;
}
#app .q-music li .item-price i{
  font-size: 12px;
  font-style: normal;
}
.digital-list .digital-list-item .digital-list-item-box{
  width: 90%;
  padding-right: 10%;
}
.digital-wrapper .digital-main{
  width: 1180px;
  margin: 10px auto;
}
.part-hd h2{
  text-align: left;
  font-size: 24px;
  font-weight: 400;
  line-height: 58px;
  color: #333;
}
.digital-wrapper{
  margin-top: 20px;
  width: 100%;
  padding: 30px 0;
  background: #fff;
}
.digital-box{
  width: 1180px;
  text-align: left;
  margin: 0 auto;
}



