
.swiper-container-digital{
   width: 1080px;
   margin: 20px auto;
}
.imgbox-wrapper .img-box{
   position: relative;
   /*width: 224px;*/
   max-height: 295px;
   overflow: hidden;
}
.imgbox-wrapper .img-box img{
   transition: all .5s;
   margin-top: 0;
}
.img-box:hover .icon-play{
   -webkit-transform: scale(1);
           transform: scale(1);
}
.img-box:hover img{
   -webkit-transform: scale(1.1);
           transform: scale(1.1);
}
.playlist-title{
   display: inline-block;
   position: relative;
   max-width: 100%;
   padding: 0 26px;
   height: 22px;
   margin: 30px 0;
   text-align: center;
   overflow: hidden;
   text-overflow: ellipsis;
}
.playlist-num{
   display: inline-block;
   width: 100%;
   text-align: center;
}
.swiper-slide .imgbox-wrapper{
   float: left;
   width: 25%;
   background: rgba(0,0,0,.8);
   height: 465px;
}
.swiper-slide .swiper-single{
   background: rgba(0,0,0,.6);
}
.swiper-slide div:hover{
   background: #42b983;
}
.swiper-slide a{
   color: #fff;
}
.recommend-wrapper .swiper{
   width: 1180px;
   margin: 0 auto;
}
.recommend-wrapper .swiper img{
   width: 100%;
}
.recommend-wrapper .swiper-pagination{
   width: 1000px;
   left: 90px;
}
.recommend-wrapper .swiper-pagination-bullet, .hot-wrapper .swiper-pagination-bullet{
   width: 30px;
   margin-right: 10px;
   background: #FFFFFF;
   height: 2px;
   border-radius: 0;
}
.recommend-wrapper .swiper-pagination-bullet:hover,.hot-wrapper .swiper-pagination-bullet:hover{
   opacity: 1;
}



