
.song-name .to-song{
  cursor: pointer;
}
.song-name .to-song:hover{
  color: #42b983;
}
.toplist-switch i {
  font-style: normal;
  font-family: cursive;
  cursor: pointer;
}
.toplist-switch i:hover {
  color: #42b983;
}
.toplist-switch .dis{
  cursor: not-allowed;
  color: #cccccc;
}
.toplist-switch .dis:hover{
  color: #ccc;
}
#app .songlist div:hover em {
  display: block;
}
#app .songlist .song-name div {
  width: 100px;
}
#app .songlist .song-name em {
  display: block;
  width: 37px;
  height: 37px;
  background: url("./qmusic/icon_list_menu.png");
  position: absolute;
  top: 18px;
  display: none;
  cursor: pointer;
}
.song-name img {
  top: 5px;
  position: absolute;
  display: inline-block;
  left: 5px;
}
#app .songlist .song-singer,#app .songlist .song-time{
  text-indent: 10px;
}
#app .songlist .song-singer span{
  cursor: pointer;
}
#app .songlist .song-singer span:hover{
  color: #42b983;
}
#app .songlist .song-name .play {
  background-position: left top;
  right: 100px;
}
#app .songlist .song-name .play:hover {
  background-position: -40px top;
}
#app .songlist .song-name .add {
  background-position: left -80px;
  right: 50px;
}
#app .songlist .song-name .add:hover {
  background-position: -40px -80px;
}
#app .q-music .songlist .rank {
  margin-left: 10px;
  font-size: 14px;
  position: relative;
  line-height: 94px;
  width: 38px;
  font-size: 12px;
}
.song-container .only {
  font-size: 12px;
  display: inline-block;
  padding: 1px 2px;
  border: 1px solid #42b983;
  border-radius: 2px;
  height: 14px;
  line-height: 14px;
  position: relative;
  top: -1px;
  color: #42b983;
}
.icon-rank {
  width: 15px;
  height: 15px;
  display: inline-block;
  background: url("./qmusic/icon_rank.png") left -60px;
  position: absolute;
  top: 28px;
  left: 12px;
}
.rise-rank {
  background-position: left top;
}
.decline-rank {
  background-position: left -15px;
}
.flat-rank {
  height: 2px;
  top: 38px;
  background-position: left -30px;
}
.new-rank {
  width: 23px;
  height: 8px;
  top: 36px;
  left: 9px;
  background-position: left -40px;
}
.special-rank {
  width: 15px;
  height: 15px;
  background-position: left -60px;
  top: 23px;
  left: 12px;
}
.top-num {
  width: 40px;
  text-align: right;
  font-size: 24px;
  font-family: poppin, 'PingFang SC', Tahoma, Arial, \5FAE\8F6F\96C5\9ED1, sans-serif;
}
#app .q-music .songlist div .front-top {
  color: #ff4222;
}
.top-songlist {
  margin-top: 10px;
}
#app .q-music .top-songlist .song-head {
  height: 50px;
  line-height: 50px;
  background-color: #fbfbfd;
  color: #999;
  padding-left: 120px;
}
#app .q-music .song-head li {
  float: left;
}
.song-container {
  width: 855px;
}
#app .songlist li {
  width: 100%;
  height: 80px;
  line-height: 80px;
  background: #fff;
  color: #333;
  float: none;
}
#app .q-music .songlist div {
  float: left;
  height: 80px;
  line-height: 80px;
  background-color: #fff;
  color: #999;
}
#app .q-music .songlist .double {
  background-color: #fbfbfd;
}
.songlist .rank-wrapper {
  width: 120px;
}
#app .q-music .songlist .song-name {
  color: #333;
  font-size: 14px;
}
#app .q-music .songlist .song-name, #app .q-music .song-head .song-name {
  width: 72%;
  position: relative;
}
#app .q-music .songlist .song-singer {
  color: #333;
  font-size: 14px;
}
#app .q-music .songlist .song-singer, #app .q-music .song-head .song-singer {
  width: 18%;
}
#app .q-music .songlist .song-time, #app .q-music .song-head .song-time {
  width: 9%;
  margin: 0;
}
.popup {
  position: absolute;
  width: 580px;
  background: #fff;
  box-shadow: 0 0 4px rgba(0, 0, 0, .35);
  border-radius: 4px;
  border: 1px solid #ddd;
  line-height: 1.2;
  padding: 10px;
  font-size: 14px;
  margin-top: -10px;
  z-index: 10;
}
.play-all {
  display: inline-block;
  width: 120px;
  height: 35px;
  text-align: center;
  color: #fff;
  background: #42b983;
  line-height: 35px;
  cursor: pointer;
  font-size: 14px;
  border-radius: 4px;
}
.toplist-hd a {
  color: #333;
  font-size: 14px;
}
.toplist-hd a:hover {
  color: #42b983;
}
.update-time {
  font-size: 14px;
  color: #333;
  margin: 0 5px;
}
.toplist-hd {
  height: 64px;
  line-height: 64px;
  margin-bottom: 10px;
}
.toplist-title {
  float: left;
  font-weight: 400;
  font-size: 24px;
  margin-right: 15px;
}
.top-list-wrapper {
  width: 100%;
  text-align: left;
  padding: 30px 0;
  margin-top: 30px;
  background: #fff;
}
.top-list-container {
  width: 1180px;
  margin: 0 auto;
}
.top-list-nav {
  width: 178px;
  border: 1px solid #cccccc;
  text-align: center;
  float: left;
  min-height: 100px;
}
.top-list-nav dl dt {
  line-height: 60px;
  font-size: 20px;
  font-weight: 400;
  border-bottom: 1px solid #ebebeb;
  margin: 0 17px 10px;
}
.top-list-nav dl dd {
  color: #333;
  background-color: #fff;
  font-size: 16px;
  line-height: 2.5;
  cursor: pointer;
}
.top-list-nav dl .current {
  background: #42b983;
  color: #fff;
}
.top-list-nav dl dd:hover {
  color: #42b983;
}
.top-list-nav dl .current:hover {
  color: #fff;
}
.toplist-mod {
  width: 975px;
  margin-left: 20px;
  float: left;
  min-height: 100px;
}



