
.flip {
  font-family: poppin, 'PingFang SC', Tahoma, Arial, \5FAE\8F6F\96C5\9ED1, sans-serif;
}
.page-wrapper {
  clear: both;
  text-align: center;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.page-wrapper .page {
  font-size: 16px;
  font-weight: 400;
  font-family: cursive;
  display: inline-block;
  line-height: 50px;
  height: 50px;
  margin: 0 5px;
  padding: 0 21px;
  box-sizing: border-box;
  min-width: 24px;
  color: #a2a2a2 !important;
}
.page-wrapper .active {
  background: #42b983;
  color: #fff !important;
}
.page-wrapper .page:hover {
  background: #42b983;
  color: #fff !important;
}
.page-wrapper .disabled {
  cursor: default;
}
.page-wrapper .disabled:hover {
  background: #fff;
  color: #a2a2a2 !important;
}



