
.summary {
  width: 300px;
  height: 500px;
  position: absolute;
  background: #cab192;
  color: #fff;
  text-shadow: 1px 1px 2px #ccc;
}
.publisher {
  position: absolute;
  bottom: 2px;
  right: 2px;
}
.summary p {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space:;
  padding: 5px;
  text-align: left;
}
.search {
  width: 100px;
  height: 33px;
  text-align: center;
  line-height: 33px;
  display: inline-block;
  background: #2ad487;
  color: #fff;
  cursor: pointer;
  position: relative;
  top: 1px;
}
img {
  max-width: 100%
}
.go-page {
  width: 50px;
  position: absolute;
  top: 0;
  right: 670px;
  height: 33px;
}
.page-num {
  position: absolute;
  top: 0;
  right: 633px;
  width: 30px;
  text-align: center;
}
ul.pageCon li.active {
  background: #2ad487
}
a {
  text-decoration: none;
  cursor: pointer;
}
.page .dis {
  background: #999999;
  cursor: not-allowed;
}
.left {
  float: left;
}
.right {
  float: right;
}
.icon {
  width: 16px;
  height: 16px;
  vertical-align: middle;
}
.clearfix {
}
.tag_title {
  padding: 2px 4px;
  border-radius: 2px;
  background: #2ad487;
  color: #fff;
  margin: 2px 2px 0 0;
  display: inline-block;
}
.clearfix::after {
  content: "";
  width: 100%;
  height: 0;
  clear: both;
  display: block;
}
.content {
  width: 1200px;
  margin: 0 auto;
}
h1 {
  color: #2ad487;
  line-height: 80px;
  border-bottom: 3px solid #2ad487;
  background-color: #222;
  text-indent: 20px;
  margin-bottom: 20px;
  text-align: left;
}
ul.movie_list li {
  background: #222;
  float: left;
  width: 280px;
  margin: 10px; /*#2ad487*/;
  padding: 12px;
  height: 465px;
  transition: all 500ms;
  box-sizing: border-box;
  overflow: hidden;
}
ul.movie_list li:hover {
  border-color: #2ad487;
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
  -webkit-transform: translate3d(0, -2px, 0);
  transform: translate3d(0, -3px, 0);
}
ul.movie_list li .movie_list_img {
  height: 350px;
  background-size: 100% 100%;
  margin-bottom: 10px
}
ul.movie_list li .title {
  color: #ddd;
  font-weight: 700;
  font-size: 16px;
  margin-bottom: 5px;
  height: 22px;
  line-height: 22px;
  overflow: hidden;
}
ul.movie_list li .original_title {
  color: #999;
  font-size: 12px
}
.page {
  width: auto;
  height: 30px;
  margin: 20px auto;
  color: #fff;
  position: relative;
}
.pageBox {
  width: 300px;
  height: 30px;
  margin: 0px auto;
  position: relative;
  overflow: hidden;
}
.page span {
  position: absolute;
  padding: 4px 10px;
  background: #2ad487;
  z-index: 1000;
  top: 0;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.page .previous {
  left: 38%;
}
.page .next {
  right: 38%;
}
ul.pageCon {
  position: absolute;
  top: 0;
  left: 0;
  width: 200%
}
ul.pageCon li {
  float: left;
  height: 30px;
  line-height: 30px;
  background: #333;
  box-sizing: border-box;
  border: 1px solid #666;
  width: 30px;
  text-align: center;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}



