
.radio-img-box{
  position: relative;
  width: 225px;
  height: 230px;
  overflow: hidden;
}
.radio-siderbar .current{
  color: #42b983;
  background: none;
}
.radio-siderbar .current:after{
  content: "";
  position: absolute;
  height: 1px;
  width: 37px;
  overflow: hidden;
  left: 0;
  top: 50%;
  background-color: #42b983;
}
.radio-img-box img{
  transition: all .5s;
}
.radio-img-box:hover .icon-play{
  -webkit-transform: scale(1);
          transform: scale(1);
}
.radio-img-box:hover img{
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.icon-play{
  cursor: pointer;
  display: inline-block;
  position: absolute;
  width: 70px;
  height: 70px;
  left: 50%;
  top:50%;
  margin-left: -35px;
  margin-top: -35px;
  transition: all .5s;
  background: url("./qmusic/cover_play.png");
  -webkit-transform: scale(0);
          transform: scale(0);
}
.radio-container{
  margin-top: 18px;
  width: 86%;
  float: left;
  margin-left: 12%;
}
.radio-box{
  margin: 0 20px 0 20px;
}
.q-music .radio-box a{
  color: #333;
  font-size: 14px;
}
.q-music .radio-box a:hover{
  color: #42b983;
}
.radio-box p{
  color: #999;
  font-size: 14px;
}
.radio-list-wrapper{
  width: 100%;
}
#app .q-music .radio-list-item{
  width: 25%;
  float: left;
  padding-bottom: 20px;
}
.radio-title{
  position: relative;
  font-weight: 400;
  height: 20px;
  line-height: 20px;
  color: #999;
  margin-bottom: 38px;
  font-size: 15px;
}
.radio-title:after{
  content: "";
  position: absolute;
  height: 1px;
  overflow: hidden;
  left: 43px;
  right: 0;
  top: 10px;
  background-color: #ececec;
}
.radio {
  width: 100%;
  text-align: left;
  background: #fff;
  padding: 40px 0;
  margin-top: 30px;
}
.radio .radio-wrapper{
  width: 1180px;
  margin:0 auto;
}
.radio-siderbar{
  background: url("./qmusic/radio_sidebar.png") 0 1px no-repeat;
  position: fixed;
  top:210px;
  transition: all .1s;
}
.radio-siderbar .siderbar-item{
  position: relative;
  display: block;
  padding-left: 63px;
  line-height: 59px;
  font-size: 15px;
  width: 38px;
}
.radio-siderbar .siderbar-item i{
  font-style: normal;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.radio-siderbar .siderbar-item i:hover{
  color: #42b983;
}



