
.score-star i{font-style: normal;position: relative;top:-5px;
}
.score-star span {
  display: inline-block;
  cursor: pointer;
}
.score{
  font-style: normal;
  color: #FF6633;
  position: relative;
  top: -3px;
  font-size: 16px;
}
.score-star .on{
  background: url("../assets/img/star-on.png");
  background-size: contain;
}
.score-star .half{
  background: url("../assets/img/star-half.png");
  background-size: contain;
}
.score-star .off{
  background: url("../assets/img/star-border.png");
  background-size: contain;
}
.typeIn span{
  width: 18px;
  height:18px;
  display: inline-block;
  margin: 2px;
}
.typeSmall span{
  width: 16px;
  height:16px;
  display: inline-block;
  margin: 1px;
}



