
.pop-up .time-wrapper input{
  width: 44px;
  height: 26px;
  text-indent: 0;
  text-align: center;
}
.pop-up{
  text-align: left;
  position: absolute;
  width: 340px;
  height: 190px;
  border-radius: 4px;
  background: #fff;
  box-shadow: 0px 0px 8px #666;
}
.pop-up input{
  border: 1px solid #ccc;
  border-radius: 2px;
  text-indent: 6px;
}
.pop-up input:focus{
  border: 1px solid #bee9ff;
  box-shadow: 0px 0px 6px #bee9ff;
}
.pop-up input,.pop-up p{
  margin: 10px;
}
.pop-up p{
  font-size: 14px;
}
.pop-up p span{
  color: #b2b2b2;
}
.pop-btn{
  height: 34px;
  border-top:1px solid #cccccc;
  line-height: 34px;
  background: #f1f1f1;
  position: absolute;
  width: 100%;
  bottom: 0;
}
.pop-btn span{
  display: inline-block;
  width: 48px;
  height: 22px;
  border-radius: 2px;
  line-height: 22px;
  text-align: center;
  margin-left: 10px;
  font-size: 12px;
  cursor: pointer;
}
.pop-btn .sure{
  background: #2788c7;
  color: #fff;
  border: 1px solid #1d567d;
}
.pop-btn .cancel{
  background: #eee;
  color: #151515;
  border: 1px solid #888888;
}
.dot-right{
  position: absolute;
  right: -10px;
  top: 18px;
  font-size: 0;
  line-height: 0;
  border-width: 10px;
  border-color: #fff;
  border-right-width: 0;
  border-style: dashed;
  border-left-style: solid;
  border-top-color: transparent !important;
  border-bottom-color: transparent !important;
}
.dot-left{
  position: absolute;
  left: -10px;
  top: 18px;
  font-size: 0;
  line-height: 0;
  border-width: 10px;
  border-color: #fff;
  border-left-width: 0;
  border-style: dashed;
  border-right-style: solid;
  border-top-color: transparent !important;
  border-bottom-color: transparent !important;
}



