
.join-time{
  font-size: 14px;
  color: #fff;
  display: inline-block;
  font-style: normal;
}
.follow input {
  text-indent: 5px;
}
.fade-enter-active, .fade-leave-active, .fade-enter-active .confirm-wrapper, .fade-leave-active .confirm-wrapper {
  transition: all .5s;
}
.fade-enter, .fade-leave-active {
  opacity: 0;
}
.fade-enter .confirm-wrapper, .fade-leave-active .confirm-wrapper {
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
  opacity: 0;
}
.tab-music .remove, .remove {
  position: absolute;
  right: 10px;
  bottom: 10px;
  color: #fff;
  display: block;
  width: 50px;
  height: 20px;
  background: #ff6633;
  text-align: center;
  line-height: 20px;
  border-radius: 4px;
  z-index: 1000;
}
.follow .tab li {
  padding: 3px 6px;
  border-radius: 4px;
}
.follow .tab .active {
  background: #42b983;
}
.tab {
  margin-left: 350px;
}
.tab-content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  color: #ffffff;
  width: 300%;
  transition: all 0.5s;
}
.tab-content div {
  width: 100%;
}
.tab-content li {
  text-align: left;
}
.tab li {
  list-style: none;
  float: left;
  color: #ffffff;
  margin: 10px;
  cursor: pointer;
}
.tab-content .music-list li {
  height: 140px;
  padding-top: 38px;
  border: 1px solid #666;
  margin: 4px;
  overflow: hidden;
  -webkit-transform: translate3d(0, -2px, 0);
  transform: translate3d(0, -2px, 0);
}
.tab-content .left-con {
  width: 26%;
  float: left;
  margin-left: 20px;
  color: #fff;
}
.tab-content .left-con span {
  width: 60%;
  display: inline-block;
}
.tab-content .right-con {
  width: 70%;
  float: left;
  color: #2ad487;
}
.right-con span {
  display: inline-block;
  height: 20px;
  line-height: 24px;
  margin: 2px;
  border-radius: 4px;
  padding: 2px 4px;
  background: #2ad487;
  color: #FFFFFF;
}
ul.music-list li:hover {
  border-color: #2ad487;
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.1);
  -webkit-transform: translate3d(0, -2px, 0);
  transform: translate3d(0, -2px, 0);
}
.right-con .star {
  margin: 0 auto;
  float: none;
}
.clearfix:after {
  content: ".";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden
}
.clearfix {
  *+height: 1%;
}



