
.q-music .song-name a{
  color: #333;
}
.q-music .song-name a:hover{
  color: #42b983;
}
#app .q-music .similar-singer li{
  width: 20%;
}
.similar-singer p{
  color: #333;
  margin-top: 10px;
}
.similar-singer .cover{
  margin-right: 20px;
}
.similar-singer .cover div{
  margin-right: 20px;
  text-align: center;
  min-height: 195px;
  margin: 0 auto;
  background: #fbfbfd;
  padding: 10px 0 5px 0;
}
.similar-singer .cover p{
  cursor: pointer;
}
.similar-singer .cover p:hover{
  color: #42b983;
}
.similar-singer .singer-pic{
  border-radius: 50%;
  cursor: pointer;
  margin: 0 auto;
}
.album-list li .album-box{
  width: 95%;
  cursor: pointer;
  margin: 0 auto;
  box-sizing: border-box;
  transition: all .5s;
  padding: 5px;
  position: relative;
  overflow: hidden;
  border: 1px solid #fff;
  margin-top: 10px;
}
.desc{
  position: absolute;
  transition: all .5s;
  height: 100px;
  overflow: hidden;
  text-overflow: ellipsis;
  background: rgba(0,0,0,0.8);
  font-size: 14px;
  color: #fff;
  bottom: -110px;
  padding: 5px;
  left: 0;
  line-height: 1.1;
}
.album-list li .album-box:hover{
  border:1px solid #42b983;
}
.album-list li .album-box:hover .desc{
  bottom: 0;
}
.album-list li .album-box:hover img{
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.song-list-head {
  width: 100%;
}
.return{
  width: 120px;
  height: 40px;
  line-height: 40px;
  background: #42b983;
  color: #fff;
  text-align: center;
  border-radius: 4px;
  cursor: pointer;
  display: inline-block;
  margin-top: 20px;
}
.more{
  height: 40px;
  line-height: 40px;
  text-align: center;
  background: #dddddd;
  color: #333;
  margin-top: 10px;
  border: 1px solid #ccc;
  width: 1088px;
  cursor: pointer;
}
.more-dis{
  cursor: not-allowed;
  color: #999999;
}
#app .song-list-head li {
  float: left;
  color: #333;
  height: 48px;
  line-height: 48px;
  background: #fff;
}
#app .song-list-head .song-list-title{
  background: #dddddd;
}
#app .song-list-head .doubel li{
  background: #fbfbfd;
}
#app .song-list-head .song-name {
  text-indent: 20px;
  width: 47.685185%;
  position: relative;
  white-space: normal
}
#app .song-list-head div:hover em{
  display: block;
}
#app .song-list-head .song-name em{
  display: block;
  width: 37px;
  height: 37px;
  background: url("../qmusic/icon_list_menu.png");
  position: absolute;
  top: 5px;
  display: none;
  cursor: pointer;
}
#app .song-list-head .song-name .play{
  background-position: left top;
  right: 50px;
}
#app .song-list-head .song-name .play:hover{
  background-position: -40px top;
}
#app .song-list-head .song-name .add{
  background-position: left -80px;
  right: 5px;
}
#app .song-list-head .song-name .add:hover{
  background-position: -40px -80px;
}
#app .song-list-head .song-name i{
  font-style: normal;
  color: #999;
  margin-right: 10px;
}
#app .song-list-head .song-name .only{
  border-radius: 2px;
  border: 1px solid #42b983;
  margin-left: 10px;
  right: 34px;
  width: 30px;
  text-align: center;
  height: 20px;
  line-height: 20px;
  text-indent: 0;
  font-size: 12px;
  top: 9px;
  color: #42b983;
}
#app .song-list-head .song-album {
  padding-left: 20px;
  width: 23.611111%;
  overflow: hidden;
  text-overflow: ellipsis;
}
#app .song-list-head .song-album a{
  color: #666;
}
#app .song-list-head .song-album a:hover{
  color: #42b983;
}
#app .song-list-head .song-time-wrapper {
  width: 26%;
}
#app .song-list-head .song-time {
  width: 50px;
}
.singer-wrapper {
  width: 100%;
  background: #f5f5f5;
  margin-top: 30px;
}
.singer-detail {
  width: 1100px;
  margin: 0 auto;
  text-align: left;
  background: #f5f5f5;
}
.singer-detail .singer-avatar {
  border-radius: 50%;
  display: block;
  float: left;
}
.singer-detail .singer-info {
  float: left;
  margin-left: 50px;
}
.singer-title {
  padding: 20px 0;
}
.singer-info h2 {
  font-size: 32px;
  margin: 40px 0 10px 0;
}
.singer-info span {
  font-size: 18px;
  margin-right: 10px;
}
.singer-info span strong {
  font-size: 22px;
  font-weight: normal;
}



