
.nothing{
  text-align: center;
  color: #fff;
}
.my-music-wrapper .song-list-header div,.song-list-item div{
  height: 50px;
  line-height: 50px;
  float: left;
  text-align: left;
  background: #f5f5f5;
  font-size: 14px;
  width: 30%;
  position: relative;
  padding: 0 10px;
}
.my-music-wrapper .item-title{
  position: absolute;
  cursor: pointer;
}
.my-music-wrapper .song-list-item img{
  width: 40px;
  cursor: pointer;
  margin: 10px;
}
.my-music-wrapper  .song-list-item div{
  height: 60px;
  line-height: 60px;
  background: #fff;
}
.my-music-wrapper   .i-song-list .bg-item div{
  background: #f6f6f6;
}
.my-music-wrapper .song-list-header .end-div,.song-list-item .end-div{
  text-align: right;
}
.my-music-wrapper   .my-nav{
  width: 1180px;
  margin: 0 auto;
  text-align: left;
  color: #fff;
  position: relative;
  bottom: -130px;
}
.my-music-wrapper .my-nav span{
  margin-right: 15px;
  cursor: pointer;
}
.my-music-wrapper  .my-nav .active{
  color: #42b983;
}
.my-music-wrapper .my-info{
  width: 200px;
  margin: 0 auto;
  padding-top: 80px;
  text-align: center;
  color: #fff;
  height: 138px;
}
.my-music-wrapper  .my-info img{
  width: 100px;
  border-radius: 50%;
  border: 3px solid #fff;
  display: block;
  margin: 0 auto;
  margin-bottom: 10px;
  font-size: 18px;
}
.my-music-wrapper .profile-wrapper{
  width: 100%;
  height: 380px;
  position: relative;
  background: url("https://y.gtimg.cn/mediastyle/yqq/img/bg_profile.jpg") no-repeat;
}
.my-music-wrapper  .my-music-wrapper{
  margin-top: 20px;
  width: 100%;
  background: #fff;
}
.my-music-wrapper  .my-music-box{
  padding-top: 20px;
  width: 1180px;
  text-align: left;
  margin: 0 auto;
}



