
#app .qmusic-list .to-singer{
  cursor: pointer;
}
.to-song{
  cursor: pointer;
}
.to-song:hover{
  color: #42b983;
}
#app .qmusic-list .to-singer:hover{
  color: #42b983;
}
.none-info{
  text-align: center;
}
.player-song-list{
  max-height: 600px;
  overflow: auto;
}
.qmusic-list i{
  font-style: normal;
}
.qmusic-list .wave{
  display: inline-block;
  width: 10px;
  height: 10px;
  background: url("./wave.gif") no-repeat;
}
.qmusic-list em{
  display: block;
  width: 37px;
  height: 37px;
  background: url("../qmusic/icon_list_menu.png");
  position: absolute;
  top: 6px;
  display: none;
  cursor: pointer;
}
.qmusic-list .play{
  background-position: left top;
  right: 10px;
}
.qmusic-list .del{
  background-position: left -160px;
  right: 10px;
}
.qmusic-list .del:hover{
  background-position: -40px -160px;
}
.qmusic-list .play:hover{
  background-position: -40px top;
}
.music-list-wrapper .qmusic-list li{
  height: 50px;
  line-height: 50px;
  border-bottom: 1px solid #fff;
}
.qmusic-list li span{
  position: relative;
}
.music-list-wrapper .qmusic-list li:hover em{
  display: block;
}
.music-list-wrapper .qmusic-list li span{
  display: inline-block;
  width: 32%;
  text-indent: 5px;
  height: 50px;
  line-height: 50px;
  overflow: hidden;
  text-overflow: ellipsis;
}



