
.singer_list_txt {
  margin-right: -20px;
  overflow: hidden;
  margin-bottom: 60px;
}
#app .q-music .singer_list_txt_item {
  float: left;
  width: 19%;
  padding: 10px 0 10px 10px;
  font-size: 14px;
  overflow: hidden;
  text-overflow: ellipsis;
  color: #333;
  cursor: pointer;
}
#app .q-music .singer_list_txt_item:hover {
  color: #42b983;
}
.singer-list-wrapper {
  width: 100%;
  background: #FFFFFF;
}
#app .singer {
  max-width: 1200px;
  margin: 0 auto;
  background: #fff;
}
#app .singer .singer_list_item {
  float: left;
  width: 20%;
  margin-bottom: 20px;
  overflow: hidden;
  cursor: pointer;
}
.singer .singer-box {
  position: relative;
  margin-right: 20px;
  background-color: #fbfbfd;
  min-height: 195px;
  padding: 25px 0;
  overflow: hidden;
  text-align: center;
}
.singer .singer-box img{
  transition: all .5s;
}
.singer .singer-box:hover img{
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
}
.singer_list_title {
  font-size: 16px;
  font-weight: 400;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #333;
  zoom: 1;
  margin: 20px 30px 4px;
}
.singer_list_title:hover {
  color: #42b983;
}
.music-group {
  background-color: #fbfbfd;
  padding: 34px 0 17px 40px;
  margin: 30px 0 20px;
}
.singer-pic {
  border-radius: 50%;
  display: block;
  width: 140px;
  height: 140px;
  margin-left: 38px;
}
#app .q-music .music-group li {
  height: 26px;
  width: auto;
  line-height: 27px;
  overflow: hidden;
  padding: 0 8px;
  margin: 0 12px 14px 0;
  color: #333;
  cursor: pointer;
}
#app .q-music .music-group li:hover {
  color: #42b983;
}
#app .q-music .music-group li.active {
  background: #42b983;
  color: #fff;
}
.singer {
  width: 100%;
  height: 100%;
  background: #fff;
  margin-top: 10px;
  text-align: left;
}
.slide-enter-active {
  transition: all .5s
}
.slide-leave-active{
  transition: all .1s
}
.slide-enter{
  -webkit-transform: translate3d(100%, 0, 0);
          transform: translate3d(100%, 0, 0)
}
.slide-leave-to{
  -webkit-transform: translate3d(-100%, 0, 0);
          transform: translate3d(-100%, 0, 0)
}
.fade-in-enter-active, .fade-in-leave-active {
  transition: all .5s
}
.fade-in-enter, .fade-in-leave-to {
  opacity: 0;
}




