
.company-detail-album{
  margin-top: 20px;
  width: 1200px;
  margin: 0 auto;
}
.company-detail-album h2{
  text-align: left;
  font-weight: normal;
  margin:20px 0;
}
.company-img{
  width: 160px;
  height: 160px;
  margin: 50px auto;
}
.company-img p{
  text-align: center;
  color: #fff;
  font-size: 24px;
}
.company-img img{
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.company-detail-wrapper{
  padding-top: 20px;
  margin-top: 20px;
  width: 100%;
  background: #fff;
  background-image: url(https://y.gtimg.cn/mediastyle/event/template_record_company/img/bg_top.jpg?max_age=2592000&v=f7e5061002c451bbea324afeadb31688);
  background-position: center 20px;
  background-repeat: repeat-x;
  min-height: 500px;
}
.company-detail-box{
  width: 1180px;
  text-align: left;
  height: 334px;
  margin: 0 auto;
}
.company-detail-head{
  width: 100%;
  height: 320px;
}



