
#app .q-music .detail-contain li{
  float: left;
  width: 20%;
}
.detail-img{
  width: 216px;
  height: 220px;
  position: relative;
  overflow: hidden;
}
.detail-img img{
  transition: all .5s;
}
.detail-img:hover .icon-play{
  -webkit-transform: scale(1);
          transform: scale(1);
}
.detail-img:hover img{
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.detail-hd{
  height: 60px;
  line-height: 60px;
  margin-top: 10px;
}
.detail-hd h3{
  float: left;
  font-size: 24px;
  font-weight: 400;
}
.detail-hd .cate{
  float: left;
  position: relative;
  padding: 0 20px 0 10px;
  margin-right: 10px;
  height: 40px;
  text-align: left;
  font-size: 14px;
  line-height: 40px;
  top:10px;
  border: 1px solid #c9c9c9;
}
.detail-hd .cate i{
  font-style: normal;
}
.detail-hd .cate:hover{
  background: #42b983;
  color: #fff;
  border: 1px solid #42b983;
}
.detail-hd .cate span{
  cursor: pointer;
  font-size: 20px;
  margin-left: 5px;
  position: absolute;
  top:1px;
  right: 5px;
}
.item-box{
  margin:0 10px;
  padding-bottom: 32px;
}
.item-box p{
  font-size: 14px;
  color: #333;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.item-box .singer{
  color: #999;
  height: 30px;
  line-height: 30px;
}
.item-box .listen-num{
  color: #999999;
  text-align: left;
}
.part-switch{
  float: right;
}
.part-switch span{
  display: inline-block;
  font-size: 14px;
  font-weight: normal;
  width: 62px;
  height: 40px;
  text-align: center;
  cursor: pointer;
  line-height: 40px;
  margin-top: 10px;
  border: 1px solid #ccc;
}
.part-switch .active{
  background: #42b983;
  border: 1px solid #42b983;
  color: #fff;
}
.playlist-tag{
  padding-top: 30px;
}
.hide{
  display: none;
}
.popup-tag{
  position: absolute;
  width: 100%;
  top: 139px;
  text-align: left;
  left: 0px;
  box-shadow: 0 0 10px rgba(183,183,183,.65);
  border: 0 none;
  background-color: #fff;
  padding: 19px 0 15px;
  z-index: 4;
}
.popup-tag span{
  max-width: 60px;
  font-size: 14px;
  padding: 4px 18px;
  white-space: nowrap;
  overflow: hidden;
  display: inline-block;
  text-overflow: ellipsis;
  color: #333333;
  cursor: pointer;
}
.popup-tag .active{
  background: #42b983;
  border: 1px solid #42b983;
  color: #fff;
}
.popup-tag .active:hover{
  color: #fff;
}
.popup-tag span:hover{
  color: #42b983;
}
#app .q-music .playlist-tag-list li{
  max-width: 60px;
  font-size: 14px;
  padding: 4px 8px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: #333333;
  cursor: pointer;
}
#app .q-music .playlist-tag-list .more{
  position: absolute;
  right: 10px;
  bottom: 0px;
  line-height: 24px;
  border-radius: 2px;
  height: 20px;
  background: #fff;
  border: none;
}
.more span{
  font-size: 24px;
  position: absolute;
  top:3px;
}
#app .q-music .playlist-tag-list .active{
  background: #42b983;
  color: #fff;
}
#app .q-music .playlist-tag-list .active:hover{
  color: #FFFFFF;
}
#app .q-music .playlist-tag-list li:hover{
  color: #42b983;
}
.playlist-tag-list i{
  position: absolute;
  width: 1px;
  background: #eeeef0;
  height: 100%;
  right: 0;
  top: 0;
}
.playlist {
  width: 100%;
  background: #fff;
  padding-bottom: 40px;
  margin-top: 30px;
}
.playlist-wrapper {
  width: 1180px;
  margin: 0 auto;
  text-align: left;
  position: relative;
}
.playlist-tag-list{
  width: 20%;
  float: left;
  position: relative;
}
.playlist-tag-list h4{
  color: #999999;
  font-weight: 400;
  font-size: 14px;
  padding-left: 8px;
  margin-bottom: 8px;
}



