
.player-img span{
  position: relative;
}
.player-img span:after{
  content: "";
  position: absolute;
  left: 9px;
  top: -169px;
  z-index: -1;
  width: 201px;
  height: 180px;
  background: url(./album_cover_player.png);
}
.song-lyric{
  position: absolute;
  top:3px;
  left: 0;
  z-index: -1;
  text-align: center;
  width: 90%;
}
.lyric-wrapper{
  margin-top: 20px;
  height: 210px;
  overflow: hidden;
}
.lyric-wrapper .lyric-box{
  width: 100%;
}
.lyric-wrapper p{
  line-height: 34px;
}
.lyric-box{
  transition: all .5s;
}
.lyric-wrapper .current{
  color: #31c27c;
  background: none;
}
.music-list-wrapper h4{
  position: relative;
}
.music-list-wrapper h4 .clear{
  text-align: center;
  display: inline-block;
  border: 1px solid #fff;
  cursor: pointer;
  position: absolute;
  right: 10px;
  opacity: .8;
  top: 0;
  font-size: 14px;
  padding: 6px 14px;
}
.music-list-wrapper h4 .clear:hover{
  opacity: 1;
}
::-webkit-scrollbar/*整体部分*/
{
  width: 10px;
  height:10px;
}
::-webkit-scrollbar-track/*滑动轨道*/
{
  -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
  border-radius: 0px;
  background: rgba(255,255,255,0.1);
}
::-webkit-scrollbar-thumb/*滑块*/
{
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
  background: rgba(255,255,255,0.2);
}
::-webkit-scrollbar-thumb:hover/*滑块效果*/
{
  border-radius: 5px;
  -webkit-box-shadow: inset 0 0 5px rgba(0,0,0,0.2);
  background: rgba(255,255,255,0.4);
}
.music-list-wrapper h4 span{
  display: inline-block;
  margin: 10px;
  padding: 4px 10px;
  border: 1px solid #fff;
  border-radius: 4px;
  cursor: pointer;
}
.music-list-wrapper h4 .slt{
  background: #fff;
  color: #42b983;
}
.song-detail{
  float: left;
  width: 36%;
  text-align: center;
}
.song-detail .lyric-wrapper{
  overflow: hidden;
  height: 210px;
}
.song-detail .lyric-wrapper p{
  width: 100%;
}
.song-info img{
  width: 186px;
  height: 186px;
  margin-top: 50px;
}
.progress-bar-wrapper{
  float: left;
  width: 66%;
}
.play-mod{
  float: left;
  width: 16%;
  height: 100%;
  position: relative;
}
.play-mod em{
  display: block;
  width: 27px;
  height: 22px;
  background: url("../qmusic/player.png") ;
  position: absolute;
  top: 16px;
  cursor: pointer;
}
.play-mod .mod{
  background-position: left -260px;
  left: 0;
}
.play-mod .sequen-play{
  background-position: left -260px;
}
.play-mod .random-play{
  background-position: left -74px;
}
.play-mod .singles-play{
  background-position: left -232px;
}
.play-mod .lists-play{
  background-position: left -205px;
}
.play-mod .like{
  background-position: left -96px;
  left: 40px;
}
.play-mod .volume{
  background-position: left -144px;
  left: 0px;
}
.volume-wrapper{
  position: relative;
  left: 80px;
}
.volume-line{
  width: 100px;
  height: 4px;
  border-bottom: 2px solid #fff;
  cursor: pointer;
  position: absolute;
  top:21px;
  left: 28px;
}
.volume-line p{
  height: 2px;
  position: absolute;
  left: 0;
  top: 4px;
  width: 100px;
  background: #42b983;
}
.volume-circle{
  position: absolute;
  width: 10px;
  height: 10px;
  border-radius: 5px;
  right: 0;
  top:0;
  background: #fff;
}
.play-mod .slt-like{
  background-position: -30px -96px;
}
.toggle{
  position: absolute;
  right: 40px;
  bottom: 20px;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.music-list-wrapper{
  width: 55%;
  padding: 20px 50px 0 50px;
  float: left;
}
.music-list-wrapper h4{
  text-align: center;
  font-weight: normal;
  margin: 10px;
}
.song-info{
  width: 100%;
  padding: 5px ;
  margin: 0 auto;
}
.progress-bar{
  width: 85%;
  cursor: pointer;
  margin: 0 auto;
  position: relative;
  height: 4px;
  padding-bottom: 2px;
  border-bottom: 2px solid #fff;
}
.progress-bar p{
  cursor: pointer;
  width: 0%;
  transition: all .1s;
  position: absolute;
  left: 0;
  top:6px;
  height: 2px;
  background: #42b983;
}
.progress-bar .dot{
  cursor: pointer;
  transition: all .1s;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: #fff;
  position: absolute;
  left: 0;
  top: 1px;
}
.progress-bar-wrapper .song-info .song-name{
  font-size: 16px;
  max-width: 180px;
  height: 20px;
  line-height: 20px;
  text-overflow: ellipsis;
  overflow: hidden;
  display: inline-block;
  white-space: nowrap;
}
.song-name i{
  color: #fff;
  cursor: pointer;
  font-style: normal;
  margin-left: 10px;
  max-width: 100px;
  height: 20px;
  text-overflow: ellipsis;
  overflow: hidden;
}
.song-name i:hover{
  color: #42b983;
}
.song-time{
  float: right;
  margin-right: 80px;
}
.player-wrapper{
  text-align: left;
   color:#fff;
}
.control-btn{
  height: 100%;
  width: 14%;
  float: left;
}
.mini-player-box{
  width: 80%;
  position: relative;
  margin: 0 auto;
}
.mini-player-box .btn-prev,.mini-player-box .btn-next{
  width: 19px;
  height: 20px;
  display: inline-block;
  background: url("./player.png");
  position: relative;
  top:10px;
}
.mini-player-box .btn-play{
  width: 21px;
  height: 29px;
  display: inline-block;
  background: url("./player.png");
  position: relative;
  top:15px;
  margin: 0 40px;
  background-position: left top;
}
.mini-player-box .btn-pause{
  background-position: -30px top;
}
.mini-player-box .btn-prev{
  background-position: left -30px;
}
.mini-player-box .btn-next{
  background-position: left -52px;
}
.player{
  position: fixed;
  top:0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  left: 0;
}
.background{
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  filter:blur(60px);
  -webkit-filter: blur(60px);
  z-index: -1;
  opacity: .9;
}
.bg-mask{
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -3;
  top: 0;
  left: 0;
  background-color: rgba(0,0,0,.95);
}
.background img{
  width: 140%;
  height: 140%;
}
.mini-player{
  position: fixed;
  bottom:0;
  width: 100%;
  height: 60px;
  background: rgba(0,0,0,0.6);
  z-index: 1000;
  left: 0;
}



