
.arrow{
  position: absolute;
  top: 100px;
  right: -11px;
  width: 11px;
  height: 17px;
  background:url("https://y.gtimg.cn/mediastyle/yqq/img/popup_arrow.png") ;
}
.about-wrapper{
  position: relative;
  min-height: 600px;
  margin-top: 20px;
}
.song-info  .singer-name:hover{
  color: #42b983;
}
.about-con{
  position: absolute;
  top: 364px;
  right: 303px;
  width: 580px;
  background: #fff;
  box-shadow: 0 0 4px rgba(0,0,0,.35);
  border-radius: 4px;
}
.popup-about{
  white-space: pre-line;
  max-height: 400px;
  font-size: 14px;
  line-height: 22px;
  margin: 30px 5px 30px 10px;
  overflow-y: auto
}
.popup-about p{
  margin-bottom: 15px;
  line-height: 18px;
}
.detail-other{
  width: 30%;
  float: left;
}
.detail-other .more{
  background: #fff;
  border: none;
  font-size: 14px;
}
.detail-other .more:hover{
  color: #42b983;
}
.detail-other .about-tit,.popup-about .about-tit{
  font-size: 20px;
  font-weight: 400;
  line-height: 46px;
}
.about-content{
  max-height: 88px;
  overflow: hidden;
  font-size: 14px;
  line-height: 22px;
  color: #333;
}
.songlist-box{
  width: 70%;
  float: left;
  min-height: 20px;
}
.about-wrapper .songlist-box p{
  font-size: 14px;
  line-height: 26px;
  color: #666666;
}
.song-info div{
  margin: 8px 0;
}
.song-detail-head .song-info div .first-span{
  width: 150px;
}
.song-detail-head .song-info div span{
  font-size: 14px;
  overflow: hidden;
  display: inline-block;
  height: 18px;
  color: #666;
}
.song-detail-head .song-info div .play-btn{
  display: inline-block;
  width: 110px;
  height: 38px;
  text-align: center;
  color: #fff;
  line-height: 38px;
  border-radius: 4px;
  background: #42b983;
  cursor: pointer;
  font-size: 16px;
  margin-top: 20px;
}
.song-info .song-name{
  font-size: 31px;
  line-height: 50px;
  font-weight: 400;
  white-space: nowrap;
  height: 50px;
  overflow: hidden;
  text-overflow: ellipsis;
}
.singer-name{
  margin-right: 2px;
}
.song-info .singer-name{
  font-size: 16px;
  color: #333;
  text-align: left;
}
.song-detail-wrapper{
  margin-top: 20px;
  width: 100%;
  background: #fff;
}
.song-detail-box{
  width: 1180px;
  text-align: left;
  margin: 0 auto;
}
.song-detail-head{
  padding-top: 50px;
}
.song-detail-head .song-img{
  width: 250px;
  height: 250px;
  float: left;
  overflow: visible;
  position: relative;
}
.song-img i{
  content: "";
  position: absolute;
  right: -35px;
  top: -2px;
  z-index: 10;
  width: 280px;
  height: 250px;
  background: url(./album_cover_player.png) no-repeat;
  background-size: contain;
}
.song-detail-head .song-info{
  float: left;
  width: 865px;
  margin-left: 50px;
}



