
.q-music li .to-song{
  color: #666666;
}
.q-music li .to-song:hover{
  color: #42b983;
}
.lyric-content{
  overflow: hidden;
  line-height: 22px;
}
.lyric-close{
   height: 120px;
}
.lyric-open{
  height: auto;
}
.lyric-wrapper{
  overflow: inherit;
  height: auto;
}
.lyric-more{
  display: inline-block;
  width: 120px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  border: 1px solid #ccc;
  border-radius: 4px;
  cursor: pointer;
  margin-top: 10px;
}
.lyric-more:hover{
  background: #f5f5f5;
}
.q-music .lyric-wrapper a{
  color: #333;
}
.q-music .lyric-wrapper a:hover{
  color: #42b983;
}
#app .wrapper-container .song-head-name:hover em{
  display: block;
}
#app .wrapper-container .song-head-name em{
  display: block;
  width: 37px;
  height: 37px;
  background: url("./qmusic/icon_list_menu.png");
  position: absolute;
  top: 5px;
  display: none;
  cursor: pointer;
}
#app .wrapper-container .play{
  background-position: left top;
  right: 100px;
}
#app .wrapper-container .play:hover{
  background-position: -40px top;
}
#app .wrapper-container .song-head-name .add{
  background-position: left -80px;
  right: 50px;
}
#app .wrapper-container .add:hover{
  background-position: -40px -80px;
}
#app .q-music .lyric-list .lyric-list-item{
  float: inherit;
  width: auto;
  font-size: 14px;
  color: #666666;
  padding:20px 0;
  border-bottom: 1px solid #ccc;
}
#app .q-music .lyric-list .lyric-list-item p{
  margin: 6px 0;
}
#app .q-music .lyric-list .lyric-list-item div{
  color: #999;
  line-height: 20px;
}
#app .q-music .lyric-list .lyric-list-item div em{
  font-style: normal;
  color: #42b983;
}
.mod-intro-singer .mv-wrapper img{
  width: 100%;
  height: 100%;
  max-height: 152px;
  transition: all .3s;
  border-radius: 0;
  margin-bottom: 10px;
}
.mod-intro-singer .mv-wrapper img:hover{
  -webkit-transform: scale(1.02);
          transform: scale(1.02);
}
.mv-list-item .mv-name span{
  color: #333;
  overflow: hidden;
  font-size: 14px;
  display: block;
  text-overflow: ellipsis;
  max-height: 18px;
  line-height: 20px;
  white-space: nowrap;
  font-weight: normal;
}
.mv-list-item p{
  color: #999;
}
.mv-list-item p em{
  font-style: normal;
  color: #42b983;
}
.wrapper-box{
  width: 100%;
  overflow: hidden;
}
.wrapper-box .wrapper-container{
  width: 400%;
  transition: all .5s;
}
.wrapper-box .wrapper{
  width: 1180px;
  float: left;
}
#app .q-music .wrapper-box .wrapper .mv-list-item{
  width: 23%;
  float: left;
  padding: 5px;
  margin-top: 15px;
}
#app .q-music .song-head ul {
  height: 50px;
  line-height: 50px;
  background-color: #fbfbfd;
  color: #999;
}
#app .q-music .song-head .double {
  background: #fff;
}
#app .q-music .song-head li {
  float: left;
  color: #999;
  font-size: 14px;
  text-indent: 12px;
  overflow: hidden;
  text-overflow: ellipsis;
  height: 50px;
}
#app .q-music .song-head .song-head-name {
  width: 47.685185%;
  position: relative;
  white-space: normal;
}
#app .q-music .song-head .song-head-single {
  padding-left: 20px;
  width: 21%;
}
#app .q-music .song-head .song-head-single span {
  margin-right: 10px;
}
#app .q-music .song-head .song-head-album {
  padding-left: 20px;
  width: 20%;
}
#app .q-music .song-head .song-head-time {
  width: 50px;
}
.mod-tab {
  text-align: left;
  margin-top: 20px;
}
.mod-tab .active {
  color: #42b983;
}
.mod-tab span {
  margin-right: 15px;
  cursor: pointer;
}
.mod-intro-singer {
  width: 1180px;
  margin: 0 auto;
  text-align: left;
}
.mod-intro-singer-wrapper {
  padding: 20px 0;
}
.mod-intro-singer-wrapper span {
  margin-left: 30px;
  font-size: 18px;
}
.mod-intro-singer img {
  border-radius: 50%;
  display: block;
  float: left;
  width: 94px;
  height: 94px;
}
.slide-down-enter-active, .slide-down-leave-active {
  transition: all .3s;
}
.slide-down-enter, .slide-down-leave-to {
  /*transform: translate3d(0, -50px, 0);*/
  -webkit-transform: scaleY(0.1);
          transform: scaleY(0.1);
  opacity: 0;
}
.left-box {
  width: 94px;
  float: left;
  line-height: 36px;
}
.right-box a {
  color: #666666;
}
.right-box {
  float: left;
  border-left: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  text-align: left;
  width: 457px;
}
.right-box p {
  text-indent: 20px;
  cursor: pointer;
  height: 36px;
  line-height: 36px;
  transition: all .3s;
  overflow: hidden;
  text-overflow: ellipsis;
}
.right-box p span {
  transition: all .3s;
  color: #42b983;
}
.right-box p:hover {
  background: #42b983;
  color: #fff;
}
.right-box p:hover span {
  color: #fff;
}
.no-border {
  border-bottom: none;
}
.mod-search-tips {
  margin-top: 20px;
  color: #fff;
}
.mod-search-tips span {
  color: #fff;
  cursor: pointer;
  display: inline-block;
  margin-right: 10px;
  font-family: poppin, 'PingFang SC', Tahoma, Arial, \5FAE\8F6F\96C5\9ED1, sans-serif;
}
.search-wrapper {
  background: #FFFFFF;
  padding-top: 20px;
  margin-top: 20px;
}
.mod_search {
  background-image: url(//y.gtimg.cn/mediastyle/yqq/img/bg_search.jpg);
  position: relative;
  height: 207px;
  background-position: 50%;
  background-size: cover;
  padding-top: 60px;
}
.mod_search_input {
  position: relative;
  width: 554px;
  margin: 0 auto;
}
.mod_search_result {
  -webkit-transform-origin: center top;
          transform-origin: center top;
  position: absolute;
  width: 552px;
  min-height: 100px;
  background: #FFFFFF;
  top: 50px;
  border: 1px solid #ccc;
  z-index: 10;
}
.mod_search_input input {
  width: 554px;
  height: 50px;
  line-height: 50px;
  border: 0 none;
  font-size: 14px;
  background: #fff;
  border-radius: 4px;
  text-indent: 20px;
  font-size: 16px;
  transition: width .6s ease-out, visibility .6s 1ms ease-out;
}
.search_btn {
  width: 50px;
  height: 50px;
  display: inline-block;
  margin-left: -54px;
  background: #42b983;
  color: #fff;
  text-align: center;
  border: 0 none;
  border-radius: 4px;
  cursor: pointer;
  position: absolute;
  top: 0;
  right: 0;
}




