
.album-box .mv-title{
  text-align: left;
}
.public-time{
  font-size: 14px;
  color: #999;
}
.album-img-box{
  position: relative;
  /*width: 224px;*/
  height: 224px;
  overflow: hidden;
}
.album-img-box img{
  transition: all .5s;
}
.album-img-box:hover .icon-play{
  -webkit-transform: scale(1);
          transform: scale(1);
}
.album-img-box:hover img{
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.album-container{
  width: 1200px;
}
.album-container .album-box{
  padding-bottom: 35px;
  margin-right: 20px;
}
#app .q-music .album-container li{
  width: 20%;
  float: left;
}
.mv-tag .tag-wrapper .slts:hover{
  color: #fff;
}
.mv-tag .popup-tag{
  top:175px;
}
.album {
  width: 100%;
  text-align: left;
  background: #fff;
  padding: 40px 0;
  margin-top: 30px;
}
.album .album-wrapper{
  width: 1180px;
  position: relative;
  margin:0 auto;
}
.tag-wrapper i{
  font-style: normal;
  margin-left: 4px;
  font-family: cursive;
  transition: all .5s;
  display: inline-block;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
.tag-wrapper span{
  line-height: 20px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.tag-wrapper .slt,.tag-wrapper .slts{
  background: #42b983;
  color: #fff;
}
.tag-wrapper .slts i{
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
}
.tag-wrapper .slt i{
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
}
.tag-wrapper .slt:hover{
  color: #fff;
}




