
#pick-date .time-wrapper{
  border-top: 1px solid #ccc;
  padding: 5px;
}
#pick-date .btn-box span{
  border-radius: 3px;
  display: inline-block;
  width: 90px;
  height: 28px;
  line-height: 28px;
  margin:2px ;
  cursor: pointer;
}
#pick-date .btn-box .save-btn{
  background: #5cb85c;
  color: #fff;
}
#pick-date .btn-box .cancel-btn{
  background: #ccc;
  color: #fff;
}
#pick-date .time-wrapper input{
  width: 44px;
  height: 26px;
  border: 1px solid #ccc;
  margin: 2px;
  border-radius:2px;
  text-align: center;
}
#pick-date .edit-box{
  border-top:1px solid #ccc;
  padding-top: 5px;
}
#pick-date .slt-today{
  float: left;
  margin-left: 10px;
  display: inline-block;
  cursor: pointer;
}
#pick-date .slt-today:hover{
  color: #5cb85c;
}
#pick-date .clear-date{
  float: right;
  margin-right: 10px;
  display: inline-block;
  cursor: pointer;
}
#pick-date .clear-date:hover{
  color: #ff6633;
}
#pick-date {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  position: absolute;
  width: 260px;
  background: #fff;
  padding-bottom: 5px;
  z-index: 1000;
  box-shadow: 2px 2px 4px #ccc;
  border-radius: 4px;
  border: 1px solid #ccc;
}
#pick-date .today{
  background: #5cb85c;
  color: #fff;
  border-radius:2px;
}
#pick-date .today:hover{
  background: #449d44;
}
#pick-date .disabled{
  color: #999;
  /*cursor: not-allowed;*/
}
#pick-date .selected{
  background: #60caff;
  color: #fff;
  border-radius: 2px;
}
#pick-date .selected:hover{
  background: #3377cc;
  color: #fff;
}
#pick-date thead tr{
  background: #f5f5f5;
  line-height: 26px;
  height: 26px;
  padding: 0;
}
#pick-date td{
  padding: 0;
  border: none;
}
#pick-date tbody td{
  height: 30px;
  cursor: pointer;
  padding: 0;
}
#pick-date table {
  width: 100%;
}
#pick-date input {
  width: 40px;
}
#pick-date td:hover {
  background: #f5f5f5;
}
#pick-date .pick-top {
  position: relative;
  height: 36px;
  line-height: 36px;
}
#pick-date  .pick-top .pick-prev {
  position: absolute;
  left: 22px;
  top: 2px;
  cursor: pointer;
}
#pick-date .prev-year{
  position: absolute;
  left: 6px;
  top: 10px;
  cursor: pointer;
  width: 14px;
  height: 14px;
  display: inline-block;
  background: none;
  background-size: contain;
}
#pick-date .next-year{
  position: absolute;
  right: 6px;
  top: 10px;
  cursor: pointer;
  width: 14px;
  height: 14px;
  display: inline-block;
  background:none;
  background-size: contain;
}
#pick-date .pick-top input{
  border: none;
  font-size: 16px;
}
#pick-date .pick-top .pick-next {
  position: absolute;
  right: 10px;
  top: 2px;
  cursor: pointer;
}
#pick-date .pick-top .disPrev, #pick-date .pick-top .disNext{
  cursor: not-allowed;
  opacity: 0.4;
}



