
.change-enter-active, .change-leave-active {
  transition: all .5s;
}
.dis{
  cursor: not-allowed;
}
.tip-btn{
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  display: inline-block;
  border: 1px solid #2ad487;
  border-radius: 4px;
  float: right;
  cursor: pointer;
  margin-right: 10px;
  padding: 2px 4px;
  font-size: 14px;
  transition:all 0.5s;
}
.tip-btn:hover{
  border-color: #42b983;
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
}
.dis{
  cursor: not-allowed;
}
.dis:hover{
  border-color: #eee;
  -webkit-transform: translateY(2px);
          transform: translateY(2px);
}
.life{
  margin-left:10px;
}
.success{
  color: #449d44;
}
.warning{
  color:#ea9518;
}
.danger{
  color:red;
}
.change-enter td, .change-leave-active td{
  -webkit-transform: scale(0.2);
          transform: scale(0.2);
  opacity: 0
}
#wrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.close-btn{
  position: absolute;
  right: 10px;
  top:0px;
  font-size: 30px;
  transition: all .5s;
  cursor: pointer;
}
.close-btn:hover{
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.game-wrapper p{
  margin-bottom: 10px;
}
.game-wrapper .game-tip{
  position: absolute;
  bottom: 0px;
  color: #666;
  width: 100%;
  text-align: center;
}
.game-wrapper{
  transition: all .5s;
  margin: 30px auto;
  padding-top: 20px;
  background: #fff;
  width: 800px;
  height: 800px;
  border-radius: 4px;
  position: relative;
}
#vue-game{
  position: fixed;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.4);
  z-index: 100;
  top:0;
  overflow-y: auto;
}
#wrapper table {
  border: 1px solid #ccc;
  padding: 10px;
  border-radius: 8px；
}
#wrapper td {
  transition: all .5s;
  background: #5bba6e;
  color: #fff;
  font-size: 25px;
  width: 40px;
  height: 40px;
  border-radius: 4px;
  vertical-align: middle;
  text-align: center;
  cursor: pointer;
}



