}
.todo-app:before {
  content: '';
  position: absolute;
  clear: both;
  right: 0;
  bottom: 0;
  left: 0;
  height: 50px;
  overflow: hidden;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2), 0 8px 0 -3px #f6f6f6, 0 9px 1px -3px rgba(0, 0, 0, 0.2), 0 16px 0 -6px #f6f6f6, 0 17px 2px -6px rgba(0, 0, 0, 0.2);
}
.todo-app h1 {
  font-size: 36px;
  font-weight: normal;
}
.todo-list .editing .edit {
  display: block;
}
.todo-list .editing .edit {
  height: 54px;
}
.todo-list .editing label, .todo-list .editing .toggle {
  display: none;
}
.todo-list .edit {
  display: none;
}
.todo-list li .destroy {
  display: none;
  position: absolute;
  top: 5px;
  cursor: pointer;
  right: 10px;
  width: 40px;
  height: 40px;
  margin: auto 0;
  font-size: 30px;
  color: #cc9a9a;
  margin-bottom: 11px;
  transition: color 0.2s ease-out;
  border: none;
  background: none;
}
.todo-list li .destroy:after {
  content: "×";
}
.todo-list li:hover .destroy,.todo-list li:hover span {
  opacity: 1;
  display: block;
}
.todo-wrapper {
  width: 90%;
  margin: 0 auto;
  background: #f5f5f5;
  padding: 50px;
}
