}
.toggle-all {
  position: absolute;
  top: -55px;
  left: -12px;
  width: 60px;
  height: 34px;
  text-align: center;
  border: none;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  -webkit-appearance: none
}
.toggle-all:before {
  content: '❯';
  font-size: 22px;
  color: #e6e6e6;
  padding: 10px 27px 10px 27px;
}
.todoapp {
  background: #fff;
