
.mv-box{
  padding-bottom: 35px;
}
.mv-box p{
  margin-bottom: 2px;
}
.mv .mv-box .listen-num{
  color: #999;
  font-size: 14px;
  text-align: left;
}
.mv-img-box{
  position: relative;
  width: 280px;
  height: 162px;
  overflow: hidden;
}
.mv-img-box img{
  transition: all .5s;
}
.mv-img-box:hover .icon-play{
  -webkit-transform: scale(1);
          transform: scale(1);
}
.mv-img-box:hover img{
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
#app .q-music .mv-container .mv-item{
  float: left;
  width: 25%;
}
.mv .mv-title{
  font-size: 14px;
  height: 20px;
  line-height: 20px;
  color: #333;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: left;
}
.mv-singer{
  height: 20px;
  overflow: hidden;
  text-overflow: ellipsis;
}
.mv-singer span{
  display: inline-block;
  margin-right: 10px;
  cursor: pointer;
  color: #666;
  font-size: 14px;
}
.mv-singer span:hover{
  color: #42b983;
}
.mv-container{
  width: 1200px;
}
.mv-box{
  margin: 0 20px 0 0px;
}
.tag-wrapper .active{
  background: #42b983;
  border-radius: 2px;
  color: #fff;
}
.tag-wrapper .active:hover{
  color: #fff;
}
.mv {
  width: 100%;
  text-align: left;
  background: #fff;
  padding: 40px 0;
  margin-top: 30px;
}
.tag-wrapper{
  float: left;
  width: 93%;
}
.tag-wrapper span{
  cursor: pointer;
  margin:0 14px 14px 14px;
  height: 20px;
  padding: 2px 6px;
  font-size: 14px;
  color: #333;
}
.tag-wrapper span:hover{
  color: #42b983;
}
.mv-tag span{
  display: inline-block;
}
.mv-tag .tag-title{
  float: left;
  font-size: 14px;
  margin-top: 2px;
  width: 7%;
}
.mv .mv-wrapper{
  width: 1180px;
  margin:0 auto;
}



