
.detail_con {
  padding-top: 20px;
}
.img_con {
  width: 300px;
}
.movie_con {
  width: 850px;
  color: #eee;
}
.movie_con h3 {
  font-size: 24px;
  margin-bottom: 20px
}
.star{
  float: left;
  margin-top:7px;
}
.movie_con p {
  line-height: 24px;
  color: #999;
  font-size: 14px
}
.movie_con .level {
  color: #EA9518;
  border-bottom: 1px solid #222;
  font-size: 14px
}
.movie_con .level .like {
  float: left;
  margin: 10px 20px 20px 0;
  cursor: pointer;
}
.movie_con .des {
  font-size: 14px;
  padding: 20px 0;
  line-height: 24px;
  height: 140px;
  overflow: auto
}
.movie_con .play_con {
  width: 200px;
  height: 50px;
  line-height: 50px;
  background: #009451;
  color: #fff;
  border-radius: 5px;
  text-align: center;
  cursor: pointer;
  margin-top: 20px;
}
.movie_con .play_con > img {
  margin-left: -25px;
  margin-right: 10px
}



